<template>
  <div class="page trialpreson flex-v medicalrecord">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">体检记录</div>
      <div class="head-btn"
        v-if="btns.hasExport"
        data-btn-id='105002001'
        @click="exportFile">
        <img src="@/assets/img/dsochuIcon.png"
          alt="导出">&nbsp;<span>导出</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        :model="queryObj"
        class="default-form-style"
        size='small'>
        <el-form-item label="登记日期：">
          <el-date-picker value-format='yyyy-MM-dd'
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="机构名称：">
          <el-select v-model="queryObj.createOrganizationId"
            placeholder="全部"
            clearable>
            <el-option :label="item.oname"
              v-for="(item, index) in orgList"
              :value="item.sysGuid"
              :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="类别：">
          <el-select v-model="queryObj.personType"
            placeholder="全部"
            clearable>
            <el-option label="全部"
              value=""></el-option>
            <el-option label="普通居民"
              value="1"></el-option>
            <el-option label="儿童"
              value="2"></el-option>
            <el-option label="老年人"
              value="4"></el-option>
            <el-option label="孕妇 "
              value="8"></el-option>
            <el-option label="高血压"
              value="16"></el-option>
            <el-option label="肺结核"
              value="32"></el-option>
            <el-option label="糖尿病 "
              value="64"></el-option>
            <el-option label="精神病"
              value="128"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn "
            icon="el-icon-search"
            round
            @click="onsubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tabledata padding20 flex-v"
      style="padding:5px 20px 20px 20px">
      <div class="countlist flex-justify">
        <template v-for="(item, index) in countlist">
          <div class="countlist-item flex flex-item"
            :key="index"
            v-if="item.countshow">
            <div>
              <div class="itemcount"
                :class="`sizecolor${index}`">{{item.count}}</div>
              <div class="itemtitle">{{item.title}}</div>
            </div>
            <div>
              <span class="icon iconfont"
                :class="`bj-${item.icon}`"></span>
            </div>
          </div>
        </template>
      </div>
      <div class="table-box flex-item"
        style="padding:0;">
        <el-table stripe
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          height='100%'
          v-loading="loading"
          header-cell-class-name='custom-table-cell'>
          <el-table-column type="index"
            width="50"
            label="序号">
            <template v-slot='scope'>
              {{scope.$index + (pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>

          <el-table-column align="center"
            label="个人登记流水号"
            prop="sysGuid"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column align="center"
            prop="userName"
            label="姓名">
          </el-table-column>
          <el-table-column align="center"
            label="性别"
            width="80">
            <template slot-scope="scope">
              <div v-if="scope.row.sexType==1">男</div>
              <div v-else-if="scope.row.sexType==2">女</div>
              <div v-else>不详</div>
            </template>
          </el-table-column>
          <el-table-column align="center"
            label="年龄"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div>{{scope.row.age | emptyData}}</div>
            </template>
          </el-table-column>
          <el-table-column align="center"
            prop="examNum"
            label="体检次数"
            width="80">
          </el-table-column>
          <el-table-column align="center"
            label="联系电话"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <div>{{scope.row.contactPhone | emptyData}}</div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="queryObj"
        query-url="/charts/base/findExamRecordList"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="controlLoading" />
    </div>
  </div>
</template>
<script>
import { xhrDownload } from '@/lib/xhr'
import { formatDate, exportFile } from '@/lib/utils'
export default {
  name: 'medicalrecord',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      isShow: false,
      loading: false,
      dateRange: '',
      OrganizationId: '',
      queryObj: {
        startCreateDate: '',
        endCreateDate: '',
        personType: '',
        createOrganizationId,
        examStatus: '',
        sexType: ''
      },
      pageNum: 1,
      pageSize: 10,
      pageindex: '',
      countlist: [
        { title: '今日总人数（人）', count: '', icon: 'ponsercount', countshow: false },
        { title: '今日男性人数（人）', count: '', icon: 'manIcon', countshow: false },
        { title: '今日女性人数（人）', count: '', icon: 'womenIcon', countshow: false },
        { title: '今日已检人数（人）', count: '', icon: 'yijiancount', countshow: false },
        { title: '今日未检人数（人）', count: '', icon: 'weijiancount', countshow: false }
      ],
      tableData: [],
      organizationlist: []
    }
  },
  created() {
    this.init()
  },

  methods: {
    exportFile() {
      this.$confirm('', {
        title: '确定导出？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          xhrDownload('/charts/base/exportExamRecord', this.exportFileParams, 'arraybuffer')
            .then(({ data }) => {
              exportFile(data, '体检记录')
            })
            .catch(err => this.$message.error(err))
        })
        .catch(cancel => cancel)
    },

    //图表统计
    chartstatistic() {
      let url = '/charts/base/getOrgRegisterPersonsNum'
      let params = { createOrganizationId: this.queryObj.createOrganizationId }
      this.$post(url, params)
        .then(({ data }) => {
          let keys = [
            'allPersion',
            'malePersion',
            'femalePersion',
            'todayExamSuccPersion',
            'regNotExamPersion'
          ]
          this.countlist = this.countlist.map((t, i) => {
            t.count = data[keys[i]]
            t.countshow = true
            return t
          })
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },

    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
      this.exportFileParams = {
        pageNum,
        pageSize,
        ...this.queryObj
      }
    },
    controlLoading(loading) {
      this.loading = loading
    },
    onsubmit() {
      this.chartstatistic()
      this.$refs.paginationEvent.initPageList(1)
    },
    init() {
      this.chartstatistic()
      this.exportFileParams = {
        pageNum: 1,
        pageSize: 10,
        createOrganizationId: this.queryObj.createOrganizationId
      }
    }
  },
  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasExport: ~btnIds.indexOf('105002001'), // 登记按钮权限
        hasMore: ~btnIds.indexOf('105002002') // 修改按钮权限
      }
    },

    orgList() {
      return this.$store.state.orgList
    }
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.queryObj.startCreateDate = val[0]
        this.queryObj.endCreateDate = `${val[1]} 23:59:59`
      } else {
        this.queryObj.startCreateDate = ''
        this.queryObj.endCreateDate = ''
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.medicalrecord {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      margin-left: 20px;
      padding: 0 30px;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      // width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      &:first-child {
        margin-left: 0px;
      }
      .iconfont {
        font-size: 50px;
      }
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
      .sizecolor0 {
        color: #07caa6;
      }
      .sizecolor1 {
        color: #70cafe;
      }
      .sizecolor2 {
        color: #fe8470;
      }
      .sizecolor3 {
        color: #15dbd4;
      }
      .sizecolor4 {
        color: #ffc000;
      }
      .bj-ponsercount {
        color: #07caa6;
      }
      .bj-manIcon {
        color: #70cafe;
      }
      .bj-womenIcon {
        color: #fe8470;
      }
      .bj-yijiancount {
        color: #15dbd4;
      }
      .bj-weijiancount {
        color: #ffc000;
      }
    }
  }
}
</style>

